import '@@assets/styles/views/attendance/setup/form.scss';
import { ACTIVE_PAGE__ } from "@@addons/functions/active_page";
import { tokenLogin } from "@@addons/functions/login/main";
import { LitElement, html, css } from 'lit';
import { customElement, property, query, queryAll } from 'lit/decorators.js';
import './forms/schedule';


@customElement("attendance-setup-form")
export class AttendanceSetupForm extends LitElement {
  constructor() {
    super();
    ACTIVE_PAGE__();
  }

  //  @query(identifier)
  //  private _identifier?: any;

  //  @queryAll(identifier)
  //  private _identifier?: any;

  @property({ type: String })
  public email?: string;

  @property({ type: Number })
  public mId?: number;

  @property({ type: Number })
  public clientId?: number;

  async connectedCallback() {
    super.connectedCallback();
    await tokenLogin(this.email, this.mId, "client_tokenLogin");
  }

  disconnectedCallback() { }

  render() {
    return html`<attendance-setup-form-schedule CLIENT_ID="${this.clientId}"></attendance-setup-form-schedule>`;
  }

  firstUpdated() { }

  createRenderRoot() {
    return this;
  }
}


